import axios from 'axios';
import React,{useState} from 'react'
import { useNavigate } from 'react-router-dom';
//import React from 'react';
import {Link} from 'react-router-dom';
import logo from './images/logo.png';
        
import {
    MDBCarousel,
    MDBCarouselItem,
    MDBBtn
  } from 'mdb-react-ui-kit';
  import { MDBFooter, MDBContainer, MDBRow, MDBCol, MDBIcon } from 'mdb-react-ui-kit';
import { Carousel } from 'react-bootstrap';
import {
    MDBInput
  } from 'mdb-react-ui-kit';

   function Add() {
    
  const navigate=useNavigate();
    const [user_name,setId] = useState("");
    const [phone_number,setName] = useState("");
    const [email,setRollno] = useState("");
    const [password,setCollege] = useState("");

    const handleSubmit = (e) => {
        const person = {user_name,phone_number,email,password}
        e.preventDefault()
        axios.post('http://localhost:8000/posts', person).then((res)=>{
            console.log(res)
            navigate('/details');
       
  
        })
    }
  

    

 return(
    <>
    <div className='head'>
            <Link to ='/'><img src={logo} height='100px' width='100px' className='imim'></img></Link>
            <ul className='Nav_menu'>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/'><li className='ini'>Home</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/about'><li>About</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}}  to='/products'><li>Products</li></Link>
                {/* <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/cart'><li>Cart</li></Link> */}
               <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/register'><li>Register</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/cart'><li>Cart</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/contact'><li>Contact</li></Link>
            </ul>
        </div>
        <br></br>
        <br></br>
        <br></br>

    <div className='container' style={{backgroundColor:"pink"}}>
         <h2>Registration Form</h2>
         <form onSubmit={handleSubmit}>
            <div className="form-group">
            <label for="fullname">Full Name:</label>
            <input type="text" className="form-control"
             id="fullname" 
            placeholder="Enter Name" name="name" 
            value={user_name} onChange={(e)=>{setId(e.target.value)}}/>
            </div>
            <div className="form-group">
            <label for="rollno">Phone Number:</label>
            <input type="number" className="form-control" id="rollno" 
            placeholder="Enter phone number" name="rollno" 
            value={phone_number} onChange={(e)=>{setName(e.target.value)}}/>
            </div>
            <div className="form-group">
            <label for="college">Email:</label>
            <input type="email" className="form-control" id="college" 
            placeholder="Enter email" name="college" 
            value={email} onChange={(e)=>{setRollno(e.target.value)}} />
            </div>
            
            <div className="form-group">
            <label for="college">Password</label>
            <input type="password" className="form-control" id="college" 
            placeholder="Enter Password" name="college" 
            value={password} onChange={(e)=>{setCollege(e.target.value)}} />
            </div>
            <br></br>
            <button type="submit" className="btn btn-primary">Register</button>
        </form>
    </div>
    <br></br>
    <br></br>
    <br></br>
    <br></br>
    <MDBFooter bgColor='info' className='text-center text-white text-lg-left'>
      <MDBContainer className='p-4 pb-0'>
        <form action=''>
          <MDBRow className='d-flex justify-content-center'>
            <MDBCol size='auto' className='mb-4 mb-md-0'>
              <p className='pt-2'>
                <strong>Sign up for our newsletter</strong>
              </p>
            </MDBCol>

            <MDBCol md='5' size='12' className='mb-4 mb-md-0'>
              <MDBInput type='text' id='form5Example2' label='Email address' contrast />
            </MDBCol>

            <MDBCol size='auto' className='mb-4 mb-md-0'>
              <MDBBtn outline color='light'>
                Subscribe
              </MDBBtn>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>

      <div className='text-center p-3' style={{ backgroundColor: 'rgba(0, 0, 0, 0.2)' }}>
        &copy; {new Date().getFullYear()} Copyright:{' '}
        <a className='text-white' target="_blank" href='https://www.linkedin.com/in/satwika-turaga-65943b246'>
          Turaga Mani Satwika
        </a>
      </div>
    </MDBFooter>
    </>
 )
   }
        
  

export default Add