import React from 'react';
import {Link} from 'react-router-dom';
import logo from './images/logo.png';
import h1 from './images/h1.png';
import h2 from './images/h2.png';
import h3 from './images/h3.png';
import hc1 from './images/hc1.png';
import hc2 from './images/hc2.png';
import hc3 from './images/hc3.png';
import hc4 from './images/hc4.png';
import hc5 from './images/hc5.png';
import hc6 from './images/hc6.png';
import hc7 from './images/hc7.png';
import hc8 from './images/hc8.png';
import hc9 from './images/hc9.png';
import mypic from './images/img.jpeg';
import {
    MDBCarousel,
    MDBCarouselItem,
    MDBBtn
  } from 'mdb-react-ui-kit';
  import { MDBFooter, MDBContainer, MDBRow, MDBCol, MDBIcon } from 'mdb-react-ui-kit';
import { Carousel } from 'react-bootstrap';
import {
    MDBInput
  } from 'mdb-react-ui-kit';
const ContactForm = () => {
  const [formStatus, setFormStatus] = React.useState('Send')
  const onSubmit = (e) => {
    e.preventDefault()
    setFormStatus('Submitting...')
    const { name, email, message } = e.target.elements
    let conFom = {
      name: name.value,
      email: email.value,
      message: message.value,
    }
    console.log(conFom)
  }
  return (
    <>
    <div className='head'>
            <Link to ='/'><img src={logo} height='100px' width='100px' className='imim'></img></Link>
            <ul className='Nav_menu'>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/'><li className='ini'>Home</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/about'><li>About</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}}  to='/products'><li>Products</li></Link>
                {/* <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/cart'><li>Cart</li></Link> */}
               <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/register'><li>Register</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/cart'><li>Login</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/contact'><li>Contact</li></Link>
            </ul>
        </div>
    <br></br>
    <br></br>
    <br></br>

    <div className="container mt-5">
      <h2 className="mb-3">Contact or FeedBack Form</h2>
      <form onSubmit={onSubmit}>
        <div className="mb-3">
          <label className="form-label" htmlFor="name">
            Name
          </label>
          <input className="form-control" type="text" id="name" required />
        </div>
        <div className="mb-3">
          <label className="form-label" htmlFor="email">
            Email
          </label>
          <input className="form-control" type="email" id="email" required />
        </div>
        <div className="mb-3">
          <label className="form-label" htmlFor="message">
            Message
          </label>
          <textarea className="form-control" id="message" required />
        </div>
        <button className="btn btn-danger" type="submit">
          {formStatus}
        </button>
      </form>
    </div>
    <br></br>
    <br></br>
    <br></br>
    <MDBFooter bgColor='info' className='text-center text-white text-lg-left'>
      <MDBContainer className='p-4 pb-0'>
        <form action=''>
          <MDBRow className='d-flex justify-content-center'>
            <MDBCol size='auto' className='mb-4 mb-md-0'>
              <p className='pt-2'>
                <strong>Sign up for our newsletter</strong>
              </p>
            </MDBCol>

            <MDBCol md='5' size='12' className='mb-4 mb-md-0'>
              <MDBInput type='text' id='form5Example2' label='Email address' contrast />
            </MDBCol>

            <MDBCol size='auto' className='mb-4 mb-md-0'>
              <MDBBtn outline color='light'>
                Subscribe
              </MDBBtn>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>

      <div className='text-center p-3' style={{ backgroundColor: 'rgba(0, 0, 0, 0.2)' }}>
        &copy; {new Date().getFullYear()} Copyright:{' '}
        <a className='text-white' target="_blank" href='https://www.linkedin.com/in/satwika-turaga-65943b246'>
          Turaga Mani Satwika
        </a>
      </div>
    </MDBFooter>
    </>
  )
}
export default ContactForm