import axios from 'axios';
import React,{useState,useEffect} from 'react'
import { useNavigate } from 'react-router-dom';
//import React from 'react';
import {Link} from 'react-router-dom';
import logo from './images/logo.png';
        
import {
    MDBCarousel,
    MDBCarouselItem,
    MDBBtn
  } from 'mdb-react-ui-kit';
  import { MDBFooter, MDBContainer, MDBRow, MDBCol, MDBIcon } from 'mdb-react-ui-kit';
import { Carousel } from 'react-bootstrap';
import {
    MDBInput
  } from 'mdb-react-ui-kit';

function Add() {
    
  let [data,setData] = useState([]);
    const navigate = useNavigate()
    useEffect(()=>{
        axios.get('http://localhost:8000/posts').then((res)=>{
            //console.log(res.data);
            setData(res.data)
        })
    })
    const reply=()=>{
        alert('This is a Cash on devlivery you will receive order soon')
    }
    const Remove = (id)=>{
        if(window.confirm('Do you want remove')){
        axios.delete("http://localhost:8000/posts/"+id)
        .then(res=>{console.log(res.data)
          window.location.reload()
        alert('Successfuly Detele')
        })
      }
      }
      const  Edit = (id)=>{
      alert("Edit the id"+id)
      navigate('/edit')
      }
  

    

 return(
    <>
    <div className='head'>
            <Link to ='/'><img src={logo} height='100px' width='100px' className='imim'></img></Link>
            <ul className='Nav_menu'>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/'><li className='ini'>Home</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/about'><li>About</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}}  to='/products'><li>Products</li></Link>
                {/* <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/cart'><li>Cart</li></Link> */}
               <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/register'><li>Register</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/cart'><li>Cart</li></Link>
                <Link style={{ color: 'inherit', textDecoration: 'inherit'}} to='/contact'><li>Contact</li></Link>
            </ul>
        </div>
<br></br>
<br></br>
<br></br>
<br></br>
<br></br>
<br></br>

<section style={{backgroundColor:'rgb(251, 190, 77)', paddingTop:'20px', paddingBottom:'30px'}}>
            <div className="container">
                {/* <center><img src={order} width='30%'></img></center> */}
                <center><h2>Order Details</h2></center>
                <table className="table">
                    <thead>
                    <tr>
                        <th>S.No</th>
                        <th>Full Name</th>
                        <th>Mail Id</th>
                        <th>Mobile Number</th>
                        <th>Food</th>
                        <th>Action</th>
                    </tr>
                    </thead>
                    <tbody>
                    {data.map((ele,index)=>{
                        return (<tr>
                        <td>{ele.id}</td>
                        <td>{ele.name}</td>
                        <td>{ele.mail}</td>
                        <td>{ele.mobile}</td>
                        <td>{ele.food}</td>
                        <td>
                        {/* <button className='btn bg-info' onClick={()=>Edit(ele.id)}>Edit</button> */}
                        <button className='btn bg-primary' onClick={()=>Remove(ele.id)}>Delete</button></td>
                    </tr>)
                    })}      
                    </tbody>
                </table>
                <center><button className="btn btn-info" onClick={reply}>Submit Order</button></center>
                </div>
          </section> 

<br></br>
<br></br>
<br></br>
<br></br>
<br></br>

    <MDBFooter bgColor='info' className='text-center text-white text-lg-left'>
      <MDBContainer className='p-4 pb-0'>
        <form action=''>
          <MDBRow className='d-flex justify-content-center'>
            <MDBCol size='auto' className='mb-4 mb-md-0'>
              <p className='pt-2'>
                <strong>Sign up for our newsletter</strong>
              </p>
            </MDBCol>

            <MDBCol md='5' size='12' className='mb-4 mb-md-0'>
              <MDBInput type='text' id='form5Example2' label='Email address' contrast />
            </MDBCol>

            <MDBCol size='auto' className='mb-4 mb-md-0'>
              <MDBBtn outline color='light'>
                Subscribe
              </MDBBtn>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>

      <div className='text-center p-3' style={{ backgroundColor: 'rgba(0, 0, 0, 0.2)' }}>
        &copy; {new Date().getFullYear()} Copyright:{' '}
        <a className='text-white' target="_blank" href='https://www.linkedin.com/in/satwika-turaga-65943b246'>
          Turaga Mani Satwika
        </a>
      </div>
    </MDBFooter>
    </>
 )
   }
        
  

export default Add